import MDBox from "components/MDBox";
import MDButton from "components/MDButton";
import MDInput from "components/MDInput";

import PropTypes from "prop-types";

function ActivityForm(props) {
  const { data } = props;

  const activity = {};

  return (
    <MDBox>
      <MDInput
        onChange={(event) => {
          activity.title = event.target.value;
        }}
        label="Title"
      />
      <br />
      <br />
      <MDInput
        onChange={(event) => {
          activity.description = event.target.value;
        }}
        label="Description"
      />
      <br />
      <br />
      <MDInput
        onChange={(event) => {
          activity.clientName = event.target.value;
        }}
        label="Client Name"
      />
      <br />
      <br />
      <MDInput
        onChange={(event) => {
          activity.clientPhone = event.target.value;
        }}
        label="Client Phone"
      />
      <br />
      <br />
      <MDInput
        onChange={(event) => {
          activity.clientEmail = event.target.value;
        }}
        label="Client Email"
      />
      <br />
      <br />
      <MDButton
        color="primary"
        onClick={() => {
          data(activity);
        }}
      >
        Create
      </MDButton>
    </MDBox>
  );
}

// Setting default values for the props of MDButton
ActivityForm.defaultProps = {
  data: () => {},
};

// Typechecking props for the MDButton
ActivityForm.propTypes = {
  data: PropTypes.func,
};

export default ActivityForm;
