// Material Dashboard 2 React components
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDBadge from "components/MDBadge";
import MDProgress from "components/MDProgress";

import { useNavigate } from "react-router-dom";

// Images
// import team2 from "assets/images/team-2.jpg";

// Import Json Data for employees
import routesObj from "routes";

import { Icon } from "@mui/material";
import TasksJson from "./tasks.json";
import Task from "../task";

export default function data() {
  const navigate = useNavigate();

  const Author = ({ name, email }) => (
    <MDBox display="flex" alignItems="center" lineHeight={1}>
      <MDBox ml={2} lineHeight={1}>
        <MDTypography display="block" variant="button" fontWeight="medium">
          {name}
        </MDTypography>
        <MDTypography variant="caption">{email}</MDTypography>
      </MDBox>
    </MDBox>
  );

  const Job = ({ title, description }) => (
    <MDBox lineHeight={1} textAlign="left">
      <MDTypography display="block" variant="caption" color="text" fontWeight="medium">
        {title}
      </MDTypography>
      <MDTypography variant="caption">{description}</MDTypography>
    </MDBox>
  );

  let rows = [];
  rows = TasksJson.map((emp) => ({
    title: <Author name={emp.title} />,
    description: <Job description={emp.description} />,
    employee: (
      <MDTypography component="a" href="#" variant="caption" color="text" fontWeight="medium">
        {emp.employee}
      </MDTypography>
    ),
    progress: (
      <MDBox width="8rem" textAlign="left">
        <MDProgress value={emp.progress} color="info" variant="gradient" label={false} />
      </MDBox>
    ),
    status: (
      <MDBox ml={-1}>
        <MDBadge
          badgeContent={emp.status}
          color={emp.progress === 100 ? "success" : "blue"}
          variant="gradient"
          size="sm"
        />
      </MDBox>
    ),

    action: (
      <MDTypography
        onClick={() => {
          routesObj.addRoute({
            type: "collapse",
            name: "task",
            key: "task",
            icon: <Icon fontSize="small">receipt_long</Icon>,
            route: "/tasks/task",
            component: <Task />,
          });
          navigate("task", { replace: true });
        }}
        component="a"
        href="#"
        variant="caption"
        color="text"
        fontWeight="medium"
      >
        View
      </MDTypography>
    ),
  }));

  return {
    columns: [
      { Header: "title", accessor: "title", align: "left" },
      { Header: "description", accessor: "description", align: "left" },
      { Header: "employee assigned", accessor: "employee", align: "center" },
      { Header: "progress", accessor: "progress", align: "center" },
      { Header: "status", accessor: "status", align: "center" },
      { Header: "action", accessor: "action", align: "center" },
    ],

    rows,
  };
}
