// @mui material components
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";

// Material Dashboard 2 React components
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDButton from "components/MDButton";

// Material Dashboard 2 React example components
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";
import Footer from "examples/Footer";
import DataTable from "examples/Tables/DataTable";

// Data
import employeesTableData from "layouts/employees/data/employeesTableData";

import "./employee.css";
import { useState } from "react";
import ActivityForm from "layouts/activities/activity_form";

function Employees() {
  const { columns, rows } = employeesTableData();
  const [showCreateActivity, setShowCreateActivity] = useState(false);

  const createActivityButtonListener = () => {
    setShowCreateActivity(!showCreateActivity);
  };

  return (
    <DashboardLayout>
      <DashboardNavbar />
      <MDBox pt={6} pb={3}>
        {/* Start of box */}

        {/* Start of grid */}
        <Grid container spacing={6}>
          <Grid item xs={12}>
            <Card>
              <MDBox
                mx={2}
                mt={-3}
                py={3}
                px={2}
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
              >
                <MDTypography variant="h6" color="white">
                  Create Activity
                </MDTypography>
              </MDBox>
              <MDBox pt={3}>
                <br />
                <div fontWeight="regular" className="create-activity">
                  Activities are created by employees. These activities can be confirmed from the
                  client and then be converted to tasks.
                </div>

                {/* Show Create button if form is hidden else show hide button */}
                <MDButton
                  component="a"
                  target="_blank"
                  rel="noreferrer"
                  variant="gradient"
                  color="primary"
                  className="create-button"
                  onClick={createActivityButtonListener}
                >
                  {showCreateActivity ? "Hide" : "Show"}
                </MDButton>

                {showCreateActivity ? (
                  <div className="activity-form">
                    <br />
                    <br />
                    <ActivityForm
                      data={(activity) => {
                        console.log(activity);
                      }}
                    />
                  </div>
                ) : null}
                <br />
              </MDBox>
            </Card>
          </Grid>
        </Grid>

        <br />
        <br />

        {/* Start of grid */}
        <Grid container spacing={6}>
          <Grid item xs={12}>
            <Card>
              <MDBox
                mx={2}
                mt={-3}
                py={3}
                px={2}
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
              >
                <MDTypography variant="h6" color="white">
                  Employees
                </MDTypography>
              </MDBox>
              <MDBox pt={3}>
                <DataTable
                  table={{ columns, rows }}
                  isSorted={false}
                  entriesPerPage={false}
                  showTotalEntries={false}
                  noEndBorder
                />
              </MDBox>
            </Card>
          </Grid>
        </Grid>
        {/* End of box */}
      </MDBox>
      <Footer />
    </DashboardLayout>
  );
}

export default Employees;
